import { Fragment, useState, useEffect } from "react";

import { connect } from "react-redux";
import moment from "moment";
import "flatpickr/dist/themes/light.css";
import { RiCloseFill } from "react-icons/ri";
import { Button, Input, Form, Modal, Badge, Row, Col, DatePicker } from "antd";
import {
  emidateAdd,
  isModelVisible,
  InputChangeValue,
} from "../../../../store/Masters/Emidates​/emidates​Slice";
import NumericInput from "../../../components/data-entry/inputs/numberInput";
export const isObjEmpty = (obj) => Object.keys(obj).length === 0;

const AddEmidates = (props) => {
  const [form] = Form.useForm();
  function ChangeHandeler(e) {
    props.InputChangeValue({
      key: e.target.name,
      value: e.target.value,
    });
  }

  // Adds New Country
  const handleAdd = () => {
    const obj = {
      id: 0,
      name: props.selectedData?.name,
      // name: moment( props.selectedData?.name).format("YYYY-MM-DD"),
      sequence: props.selectedData?.sequence,
    };
    if (obj.name != null || obj.name == "") {
      props.emidateAdd(obj);
    }
  };
  // Updates Country in Store
  const handleUpdate = () => {
    const countryToUpdate = {
      id: props.selectedData.id,
      name: props.selectedData?.name,
      sequence: props.selectedData?.sequence,
    };
    props.emidateAdd(countryToUpdate);
  };

  // Reset Input Values on Close
  const handleResetInputValues = () => {
    props.isModelVisible(false);
  };

  const FooterAddOrUpdate = () => {
    if (props.selectedData.id == null || props.selectedData.id == 0) {
      return (
        <Fragment>
          <Button
            onClick={() => {
              form.submit();
            }}
            type="primary"
            block
          >
            Add
          </Button>
        </Fragment>
      );
    } else {
      return (
        <Fragment>
          <Button
            type="primary"
            onClick={() => {
              form.submit();
            }}
            block
          >
            Update
          </Button>
        </Fragment>
      );
    }
  };

  const FooterCancel = () => {
    return (
      <Fragment>
        <Button
          onClick={() => {
            handleResetInputValues();
          }}
          type="primary"
          block
          ghost
        >
          Cancel
        </Button>
      </Fragment>
    );
  };

  const modalTitle = (
    <h5 className="modal-title">
      {props.selectedData.id == null || props.selectedData.id == 0
        ? "Add"
        : "Update"}{" "}
      Emi Date
    </h5>
  );

  useEffect(() => {
    form.setFieldsValue({
      name: props.selectedData.name,
      sequence: props.selectedData.sequence,
    });
  }, [props.selectedData]);

  return (
    <Modal
      visible={props.model}
      title={modalTitle}
      onCancel={() => {
        handleResetInputValues();
        form.resetFields();
        props.isModelVisible(false);
      }}
      className="hp-modal-p-24"
      footer={
        <Row justify="center">
          <Col span={12} className="hp-pr-4">
            <FooterCancel />
          </Col>

          <Col span={12} className="hp-pl-4">
            <FooterAddOrUpdate />
          </Col>
        </Row>
      }
      centered
      closeIcon={
        <RiCloseFill className="remix-icon text-color-black-100" size={24} />
      }
      width={416}
    >
      <Form
        layout="vertical"
        form={form}
        autoComplete="off"
        onFinish={() => {
          if (props.selectedData.id == null) {
            handleAdd();
          } else {
            handleUpdate();
          }
        }}
      >
        <Form.Item
          label="Emi Date :"
          name="name"
          rules={[{ required: true, message: "Please input your Emi Date!" }]}
          getValueProps={() => { }}
          hasFeedback
        >
          <NumericInput
            id="name"
            name="name"
            style={{ width: "100%" }}
            placeholder="Emi Date"
            maxLength={30}
            value={
              props.selectedData.name == null ? "" : props.selectedData?.name
            }
            onChange={(value) => {
              props.InputChangeValue(value);
            }}
          />
        </Form.Item>

        <Form.Item
          label="Sequence :"
          name="sequence"
          rules={[{ required: true, message: "Please input your Sequence!" }]}
          hasFeedback
        >
          <Input
            id="Sequence"
            name="sequence"
            style={{ width: "100%" }}
            placeholder="Sequence"
            value={props.selectedData?.sequence}
            onChange={ChangeHandeler}
          />
        </Form.Item>
      </Form>
    </Modal>
  );
};

function mapStateToProps({ emidates }) {
  return {
    selectedData: emidates.rowdata,
    model: emidates.model,
  };
}

export default connect(mapStateToProps, {
  emidateAdd,
  isModelVisible,
  InputChangeValue,
})(AddEmidates);
