import React, { useEffect, useState } from "react";

import { Row, Col, Input, Card, Pagination, Spin, Popconfirm } from "antd";
import { Table, Space } from "antd";
import { RiErrorWarningLine } from "react-icons/ri";
import { connect } from "react-redux";

import MasterPageTitle from "../../../../layout/components/content/master-page-title";
import {
  getEmidates,
  emidateEdit,
  emidateDelete,
  isModelVisible,
  SetEmidatesParams,
} from "../../../../store/Masters/Emidates​/emidates​Slice";
import AddEmidates from "./addEmidates​";

function Emidates(props) {
  useEffect(() => {
    props.getEmidates({ page: 1, search: "", page_size: 10 });
  }, []);

  const columns = [
    {
      title: "S.No",
      dataIndex: "index",
      width: "5%",
      // sorter: (a, b) => a.index - b.index,
    },
    {
      title: "Code",
      dataIndex: "code",
      // sorter: (a, b) => a.name.length - b.name.length,
      filterSearch: true,
      width: "35%",
    },
    {
      title: "Emi Dates",
      dataIndex: "name",

      filterMode: "tree",
      filterSearch: true,
      // sorter: (a, b) => a.name.length - b.name.length,
      width: "40%",
    },
    {
      title: "Sequence",
      dataIndex: "sequence",

      filterMode: "tree",
      filterSearch: true,
      // sorter: (a, b) => a.name.length - b.name.length,
      width: "40%",
    },
    {
      title: "Actions",
      key: "action",
      width: "10%",
      render: (row, record) => (
        <Space size="middle">
          {props.userAccessList?.indexOf("Masters.change_emidate") !== -1 && (
          <i
            className="ri-edit-box-line hp-mr-2 "
            style={{ cursor: "pointer", color: "#fc6f03" }}
            onClick={() => {
              props.emidateEdit(row);
              props.isModelVisible(true);
            }}
          ></i>
          )}

        {props.userAccessList?.indexOf("Masters.delete_emidate") !== -1 && (
          <Popconfirm
            placement="topRight"
            title="Are you sure to delete this Record?"
            onConfirm={() => props.emidateDelete(record.id)}
            okText="Yes"
            cancelText="No"
            className="hp-ml-8"
            icon={
              <RiErrorWarningLine className="remix-icon hp-text-color-primary-1" />
            }
          >
            <i
              className="ri-delete-bin-2-line "
              style={{ cursor: "pointer", color: "#ff0000" }}
            ></i>
          </Popconfirm>
        )}
        </Space>
      ),
    },
  ];

  const data = props.emidateslist.map((row, key) => ({
    ...row,
    index:
      (props.emidatesParams.page - 1) * props.emidatesParams.page_size +
      key +
      1,
  }));

  function onPageChange(page, pageSize) {
    props.SetEmidatesParams({
      ...props.emidatesParams,
      page_size: pageSize,
      page: page == 0 ? 1 : page,
    });
    props.getEmidates({
      ...props.emidatesParams,
      page_size: pageSize,
      page: page == 0 ? 1 : page,
    });
  }

  function onSearch(val) {
    props.SetEmidatesParams({ ...props.emidatesParams, search: val });
    props.getEmidates({ ...props.emidatesParams, search: val });
  }
  function showTotal(total) {
    return "Total " + total + " items";
  }
  const { Search } = Input;
  return (
    <Row gutter={[32, 32]} className="hp-mt-32">
      <MasterPageTitle
        pageTitle="Emi Dates Master"
        addModelTile={
          props.userAccessList?.indexOf("Masters.add_emidate") !== -1 &&
          "Add Emi Date"
        }
        goBack={true}
        setIsModalVisible={props.isModelVisible}
        isModalVisible={props.model}
      />

      <Col span={24}>
        <Card>
          <Row className="hp-mb-12">
            <Col span={12} xs={24} sm={24} md={14} className="hp-mb-10">
              <Search
                placeholder="Search..."
                allowClear
                onChange={(e) => {
                  if (e.target.value == "") {
                    props.getEmidates({
                      ...props.emidatesParams,
                      page: 1,
                      search: "",
                    });
                    props.SetEmidatesParams({
                      ...props.emidatesParams,
                      page: 1,
                      search: "",
                    });
                  }
                }}
                onSearch={(value) => {
                  onSearch(value);
                }}
              />
            </Col>

            <Col span={24}>
              <Spin tip="Loading..." spinning={props.loading} delay={500}>
                <Table
                  columns={columns}
                  dataSource={data}
                  scroll={{ x: 500 }}
                  pagination={false}
                  size="middle"
                />
              </Spin>
            </Col>
            <Col span={24}>
              <Pagination
                size="small"
                current={props.emidatesParams?.page}
                pageSize={props.emidatesParams?.page_size}
                total={props.listCount}
                showTotal={showTotal}
                showSizeChanger
                onChange={onPageChange}
                on
                className="hp-mb-12 hp-mt-24 hp-float-right"
              />
            </Col>
          </Row>
        </Card>
      </Col>
      <AddEmidates />
    </Row>
  );
}

function mapStateToProps({ emidates, system }) {
  return {
    emidateslist: emidates.emidateslist,
    noofpages: emidates.noofpages,
    listCount: emidates.listCount,
    emidatesParams: emidates.emidatesParams,
    loading: emidates.loading,
    userAccessList: system.userAccessList,
  };
}

export default connect(mapStateToProps, {
  getEmidates,
  emidateEdit,
  emidateDelete,
  isModelVisible,
  SetEmidatesParams,
})(Emidates);
